.skill-subsection {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
  color: rgba(255, 255, 255, 0.9);
}

.skill-subsection p {
  font-size: 1.5rem;
  font-weight: bold;
}

.skill-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  row-gap: 2rem;
  column-gap: 1rem;
}

.skill-item {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-inline: 1rem;
  padding: 1rem;
  width: 150px;
  height: 150px;
  background-color: rgb(40, 44, 52);
  border: solid 0.13rem rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  box-shadow: 0 7px 20px 5px rgba(0, 0, 0, 0.53);
  transition: all 0.25s ease-in-out;
}

.skill-item:hover {
  transform: scale(1.1);
}

.skill-item p {
  margin-top: 0.5rem;
}

.skill-icon {
  font-size: 3rem;
  width: 90px;
  height: 90px;
}
